@font-face {
    font-family: 'icons';
    font-style: normal;
    font-weight: normal;
    font-display: block;
    src: url("../../fonts/icons.woff") format("woff"), 
         url("../../fonts/icons.woff2") format("woff2");
  }
  
  [class^="_icon-"]::before,
  [class*=" _icon-"]::before {
    font-family: 'icons' !important;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  ._icon-fb:before {
    content: "\e900";
  }
  
  ._icon-ig:before {
    content: "\e901";
  }
  
  ._icon-li:before {
    content: "\e902";
  }
  
  ._icon-tw:before {
    content: "\e903";
  }
  